@import "config";

* {
  box-sizing: border-box;
}
body {
  @include background();
  background-color: $primary-color;
  color: #fff;
  margin: 0;
  min-height: 100vh;
  width: 100%;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.3;
  position: relative;
}
#root {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-start;
  min-height: 100vh;
  width: 100%;
}
h1, h2, h3 {
  margin: 0;
  font-weight: 400;
  &.lg-heading {
    font-size: 3rem;
    font-weight: bold;
  }
  &.sm-heading {
    margin-bottom: 2rem;
    padding: 0.2rem 1rem;
    background: rgba(lighten($primary-color, 2), 0.5);
  }
}
a {
  color: white;
  text-decoration: none;
}
header {
  position: fixed;
  z-index: 2;
  width: 100%;
}
.text-secondary {
  color: $secondary-color;
}
main {
  flex: 1;
  padding: 3rem;
  .icons {
    a {
      display: inline-block;
      padding: 0.4rem;
      &:hover {
        color: $secondary-color;
        transform: scale(1.1);
        @include easeOut();
      }
    }
  }
  &#home {
    overflow: hidden;
    margin-top: 20vh;
  }
}

footer {
  padding: 0.7rem;
  font-size: 0.5rem;
  color: white;
  text-align: center;
}